import { Link, Route, Routes } from "react-router-dom";
import Test from "./Test";
import "./index.css";
import CreateTest from "./CreateTest";
import RunOutTime from "./RunOutTime";
import OkPage from "./OkPage";

function App() {
  return (
    <div>
      <Routes>
        <Route path="/create" element={<CreateTest />} />
        <Route path="/:id" element={<Test />} />
        <Route path="/run-out-time" element={<RunOutTime />} />
        <Route path="/ok" element={<OkPage />} />
        <Route path="*" element={<Link to="/create">Создать тест</Link>} />
      </Routes>
    </div>
  );
}

export default App;
